*, *:before, *:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6,
blockquote, p, pre,
dl, dd, ol, ul,
figure,
hr,
fieldset, legend {
    margin: 0;
    padding: 0;
}

table {
   border-collapse: collapse;
   border-spacing: 0;
}

ul, ol {
    list-style: none;
}

img[width],
img[height] {
    max-width: 100%;
    height: auto;
}


